<template>
  <div class="table">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-lx-cascades"></i> 基础表格</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <!-- <div class="handle-box">
        <el-select v-model="select_cate" placeholder="筛选省份" class="handle-select mr10">
          <el-option key="1" label="广东省" value="广东省"></el-option>
          <el-option key="2" label="湖南省" value="湖南省"></el-option>
        </el-select>
        <el-input v-model="select_word" placeholder="筛选关键词" class="handle-input mr10"></el-input>
        <el-button type="primary" icon="search">搜索</el-button>
      </div> -->

      <el-table :data="list" border style="width: 100%"  fit v-loading="listLoading" highlight-current-row>
        <el-table-column fixed prop="id" label="id">
        </el-table-column>
        <el-table-column prop="name" label="地址">
        </el-table-column>
        <el-table-column prop="province" label="省份">
        </el-table-column>
        <el-table-column prop="city" label="市区">
        </el-table-column>
        <el-table-column prop="counties" label="地址">
        </el-table-column>
        <el-table-column prop="address" label="邮编">
        </el-table-column>
        <el-table-column prop="idCompany" label="邮编">
        </el-table-column>
        <el-table-column fixed="right" label="操作">
          <template slot-scope="scope">
            <!-- <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button> -->
            <el-button type="text" size="small">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" @prev-click='prevClick' @next-click='nextClick' background layout="total, sizes, prev, pager, next, jumper" :total='total' :page-sizes="[10, 15, 20, 50]" :page-size="size" :current-page="pageNum">
        </el-pagination>
      </div>
    </div>

    <!-- 编辑弹出框 -->
    <!-- <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
      <el-form ref="form" :model="form" label-width="50px">
        <el-form-item label="日期">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.date" value-format="yyyy-MM-dd" style="width: 100%;"></el-date-picker>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="地址">
          <el-input v-model="form.address"></el-input>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false">取 消</el-button>
        <el-button type="primary" @click="saveEdit">确 定</el-button>
      </span>
    </el-dialog> -->

    <!-- 删除提示框 -->
    <!-- <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt">删除不可恢复，是否确定删除？</div>
      <span slot="footer" class="dialog-footer"> -->
    <!-- <el-button @click="delVisible = false">取 消</el-button> -->
    <!-- <el-button type="primary" @click="deleteRow">确 定</el-button> -->
    <!-- </span>
    </el-dialog> -->
  </div>
</template>

<script>
import { fetchList } from '@/api/article'
export default {
  name: 'basetable',
  data () {
    return {
      listQuery: {
        pageNum: 0, // 当前页
        pageSize: 0 // 一页多少
      },
      listLoading: true,
      list: [],
      pageNum: 0, // 当前页
      pages: 0, // 总页数
      pageSize: 0, // 每页的数量
      size: 0, // 当前页的数量
      total: 0 // 总记录数
    }
  },
  created () {
    this.getList()
  },
  computed: {},
  methods: {
    getList () {
      this.listLoading = true
      fetchList(this.listQuery).then(response => {
        this.list = response.list
        this.pages = response.pages
        this.pageSize = response.pageSize
        this.total = response.total
        this.size = response.size
        this.pageNum = response.pageNum
        console.log(response)
        setTimeout(() => {
          this.listLoading = false
        }, 1.5 * 1000)
      })
    },
    handleSizeChange (val) {
      this.listQuery.pageSize = val
      this.getList()
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      this.listQuery.pageNum = val
      this.getList()
      console.log(`当前页: ${val}`)
    },
    // 上一页
    prevClick () {
      ++this.listQuery.pageSize
      this.getList()
    }, // 下一页
    nextClick () {
      --this.listQuery.pageSize
      this.getList()
    }
  }
}
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-select {
  width: 120px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}
.del-dialog-cnt {
  font-size: 16px;
  text-align: center;
}
.table {
  width: 100%;
  font-size: 14px;
}
.red {
  color: #ff0000;
}
</style>



